@media screen and (max-width: 1050px) {
  .text__secundari {
    font-size: 4.2rem;
  }
  .banner__paragraph p {
    font-size: 1.1rem;
    width: 60%;
  }
  .plataforma__paragraph {
    font-size: 1.1rem;
    width: 70%;
  }
}
@media screen and (max-width: 810px) {
  .display__controler {
    display: none;
  }
  .aviso__responsivo {
    display: block;
    color: aliceblue;
    text-align: center;
    position: relative;
    top: 50px;
    font-size: 1.3rem;
  }
}
