@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
.ghost__45 {
  height: 45px;
}
.ghost__120 {
  height: 70px;
}
.container__header {
  position: fixed;
  width: 100%;
  z-index: 10;
  height: 60px;
  background: rgba(0, 0, 0, 0.514);
  backdrop-filter: blur(2px);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.432);
}
#name__marca {
  width: fit-content;
  font-size: 2rem;
  font-weight: 900;
}
#name__marca a {
  text-decoration: none;
  color: #f5f5f5;
  cursor: pointer;
}
.link__header {
  color: rgb(155, 155, 155);
  text-decoration: none;
  font-size: 0.965rem;
}
.link__header:hover {
  color: #fff;
}
.lista__central {
  width: 250px;
  height: fit-content;
  display: flex;
  justify-content: space-around;
}
.lista_rigth {
  width: 170px;
  height: fit-content;
  display: flex;
  justify-content: space-evenly;
}
.item_lista {
  list-style: none;
  position: relative;
  left: -15px;
}
.border__comp {
  padding: 5px 6px 7px;
  border-radius: 6px;
  font-weight: 400;
  background-color: #f5f5f5;
  color: #000;
  transition: 0.3s ease-out;
}
.border__comp:hover {
  background: none;
  border: 1px solid #fff;
}
/*Main*/
.container__buttons {
  display: flex;
  justify-content: center;
  gap: 16px;
}
.button__central {
  font-size: 1.1rem;
  line-height: 1.5rem;
  padding: 10px 17px 12px;
  border-radius: 6px;
  font-weight: 600;
  background-color: #f5f5f5;
  color: #000;
  transition: 0.2s ease-out;
}
.button__central:hover {
  background: none;
  border: 1px solid #fff;
}
.button__central_sec {
  font-size: 1.1rem;
  line-height: 1.5rem;
  padding: 10px 17px 12px;
  border-radius: 6px;
  font-weight: 600;
  transition: 0.26s ease-out;
  background: none;
  border: 1px solid #fff;
}
.button__central_sec:hover {
  background: rgb(0, 124, 240);
  background: -moz-linear-gradient(
    90deg,
    rgba(0, 124, 240, 1) 0%,
    rgba(0, 223, 216, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(0, 124, 240, 1) 0%,
    rgba(0, 223, 216, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(0, 124, 240, 1) 0%,
    rgba(0, 223, 216, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#007cf0",endColorstr="#00dfd8",GradientType=1);
  color: #000;
  border: none;
}
.container__main {
  width: 100%;
  color: #f5f5f5;
}
.banner_name {
  position: relative;
  top: 20px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.text__secundari {
  font-size: 6rem;
  letter-spacing: -0.05em;
}
.banner__paragraph {
  justify-content: center;
  display: flex;
}
.banner__paragraph p {
  color: rgb(155, 155, 155);
  width: 50%;
  text-align: center;
  position: relative;
  bottom: 20px;
  font-size: 1.3rem;
}
/*Tecnologias*/
.container__tecnologies {
  height: fit-content;
  padding: 10px 0px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}
.tecnologies__titulo {
  color: rgb(155, 155, 155);
}
.icones__tecnologies {
  pointer-events: none;
  width: 58px;
  margin: 0px 15px;
}
/*Plataformas seção*/
.container__plataforma_secao {
  width: 100%;
  height: fit-content;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.plataforma__titulo {
  font-size: 1.5rem;
}
.plataforma__paragraph {
  width: 50%;
  text-align: center;
  font-size: 1.3rem;
  color: rgb(155, 155, 155);
}
.plataforma__dispositivos {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 30px 0px;
}
.container__dispositivos {
  background-color: #1f1f1f;
  text-align: center;
}
.web__container {
  width: 320px;
  height: 210px;
  border-radius: 17px;
  border: 3px solid #b3b3b3;
}
.mobile__container {
  border-radius: 20px;
  width: 160px;
  height: 250px;
  border: 5px solid #b3b3b3;
}
/*Footer*/
.container__footer {
  padding: 20px 0px;
  width: 100%;
  border-top: 1px solid #b3b3b3;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  color: rgb(155, 155, 155);
}
.container__lista__footer {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.container__lista__footer h4 {
  color: #f5f5f5;
}
/*Responsivos*/
.aviso__responsivo {
  display: none;
}
